.slider {
    position: relative;
    height: 200px;
    .slider-previous,
    .slider-next {
        position: absolute;
        top: 50%;
        font-size:50px;
        margin-top: -25px;
        color: #fff;
        z-index: 99;
    }
    .slider-next {
        right: 0;
    }

    .slider-box {
        position: relative;
        width: 100%;
        overflow: hidden;
        height: 100%;
        ul {
            position: absolute;
            width: 5000px;
            padding: 0;
            margin: 0;
            li {
                list-style: none;
                float: left;
            }
        }
    }

    .slider-point {
        position: absolute;
        bottom: 0;
        right: 100px;
        li {
            list-style: none;
            display: inline-block;
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background: rgba(0, 0, 0, 0.3);
            text-align: center;
            font-size: 12px;
            &.active {
                background: #fff;
            }
            span {

            }
        }
    }
    &.fade-slider {
        .slider-box {
            ul {
                li {
                    position: absolute;
                    top: 0;
                    left: 0;
                }
            }
        }
    }
}